import React, { useState, Fragment } from "react";
import { calcularTotal } from "../helpers";

const Formulario = (props) => {
  const { amount, setAmount, term, setTerm, setTotal, setLoading } = props;
  // Definimos el state
  const [error, setError] = useState(false);

  //HandleEvent
  const handleSubmit = (e) => {
    e.preventDefault();

    //Validamos
    if (amount === 0 || term === "") {
      setError(true);
      return;
    }

    //Si se valido o se corrigi칩 el error, entonces el error es false
    setError(false);
    setLoading(true);
    setTimeout(() => {
      //Realizar la cotizaci칩n
      const total = calcularTotal(amount, term);
      //Una vez calculado, guardamos el total
      setTotal(total);
      setLoading(false);
    }, 3000);
  };

  return (
    <Fragment>
      <form onSubmit={handleSubmit}>
        <div className="row">
          <div>
            <label>Cantidad Prestamo</label>
            <input
              className="u-full-width"
              type="number"
              placeholder="Ejemplo: 3000"
              onChange={(e) => setAmount(parseInt(e.target.value))}
            />
          </div>
          <div>
            <label>Plazo para Pagar</label>
            <select
              className="u-full-width"
              onChange={(e) => setTerm(parseInt(e.target.value))}
            >
              <option value="">Seleccionar</option>
              <option value="3">3 meses</option>
              <option value="6">6 meses</option>
              <option value="12">12 meses</option>
              <option value="24">24 meses</option>
            </select>
          </div>
          <div>
            <input
              type="submit"
              value="Calcular"
              className="button-primary u-full-width"
            />
          </div>
        </div>
      </form>
      {error ? (
        <p className="error">Todos los campos son obligatorios</p>
      ) : null}
    </Fragment>
  );
};

export default Formulario;
