import React from "react";
import "./Spinner.css";

const Spinner = () => {
  return (
    <div className="sk-chase">
      <div className="sk-chase-dot"></div>
      <div className="sk-chase-dot"></div>
      <div className="sk-chase-dot"></div>
      <div className="sk-chase-dot"></div>
      <div className="sk-chase-dot"></div>
      <div className="sk-chase-dot"></div>
    </div>
  );
};

export default Spinner;

/*
-Este componente "Spinner" se mostrar치 mientras loading sea true !
-Notar que estamos importando los estilos css de "Spinner.css"
*/
